html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body.jadoo {
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: #5E6282;
  padding-bottom: 100px;
}

body.jadoo .container-fluid {
  max-width: 1600px;
  margin: 0 auto;
}

body.jadoo img {
  max-width: 100%;
}

@media (min-width: 1200px) {
  body.jadoo body.jadoo .container {
    max-width: 1200px;
  }
  body.jadoo body.jadoo .container, body.jadoo body.jadoo .container-fluid, body.jadoo body.jadoo .container-lg, body.jadoo body.jadoo .container-md, body.jadoo body.jadoo .container-sm, body.jadoo body.jadoo .container-xl, body.jadoo body.jadoo .container-xxl {
    --bs-gutter-x: 3.3rem;
  }
}

.fade-up-image {
  opacity: 0;
  transition: opacity 1s ease, transform 1s ease;
}

.fade-up-image {
  transform: translateY(150px) translateX(-150px);
}

.delay-2 {
  transition-delay: 2s;
  animation-delay: 2s !important;
}

.main-menu a {
  color: #39425D;
  line-height: 1.5;
}

.main-menu a:hover, .main-menu a:active {
  color: #707b9d;
}

@media (min-width: 992px) {
  .main-menu .navbar-nav {
    align-items: center;
  }
  .main-menu .navbar-nav .nav-link.btn {
    border: 1px solid #39425D;
    padding: 7px 18px;
    color: #39425D;
    border-radius: 5px;
  }
  .main-menu .navbar-nav .nav-link.btn:hover, .main-menu .navbar-nav .nav-link.btn:active {
    border: 1px solid #707b9d;
  }
  .main-menu .nav-item {
    margin-left: 35px;
  }
  .main-menu .nav-item.menu-lang {
    margin-left: 35px;
  }
}

@media (min-width: 1200px) {
  .main-menu .nav-item {
    margin-left: 48px;
  }
  .main-menu .nav-item.menu-lang {
    margin-left: 35px;
  }
}

@media (max-width: 991px) {
  body.jadoo .navbar-nav .nav-link.btn {
    display: block;
    color: #39425D;
    padding: var(--bs-nav-link-padding-y) var(--bs-nav-link-padding-x);
    text-align: left;
  }
  body.jadoo .navbar {
    position: static;
  }
  body.jadoo .navbar-toggler {
    background-color: transparent;
    cursor: pointer;
    height: 35px;
    padding: 10px;
    position: relative;
    width: 50px;
    border: none;
    box-shadow: none;
  }
  body.jadoo .navbar-toggler span {
    background-color: #686D77;
    display: inline-block;
    height: 5px;
    position: absolute;
    right: 0;
    transition: all .3s ease;
    width: 35px;
  }
  body.jadoo .navbar-toggler span.top-line {
    top: 7px;
    width: 50px;
  }
  body.jadoo .navbar-toggler span.middle-line {
    top: 17px;
    width: 42px;
  }
  body.jadoo .navbar-toggler span.bottom-line {
    top: 27px;
  }
  body.jadoo .navbar-toggler[aria-expanded="true"] span.top-line {
    top: 15px;
    transform: rotate(-45deg);
  }
  body.jadoo .navbar-toggler[aria-expanded="true"] span.middle-line {
    opacity: 0;
  }
  body.jadoo .navbar-toggler[aria-expanded="true"] span.bottom-line {
    top: 15px;
    transform: rotate(45deg);
    width: 50px;
  }
  body.jadoo .navbar-collapse {
    background-color: #FFF1DA;
    height: 100vh;
    left: 0;
    opacity: 0;
    padding: 11px 18px 12px;
    position: absolute;
    top: 88px;
    transition: all .3s ease 0s;
    width: 100%;
    z-index: 4;
  }
  body.jadoo .navbar-collapse.show {
    opacity: 1;
  }
}

.jadoo .dropdown-toggle::after {
  background-image: url("../images/arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  width: 12px;
  height: 7px;
  margin-left: .33em;
  vertical-align: .18em;
}

body.jadoo .btn {
  font-weight: 500;
  color: white;
  font-size: 1.06rem;
  line-height: 1.59rem;
  text-align: center;
  display: inline-block;
  border-radius: 10px;
  border-color: transparent;
  padding: 16px 25px;
}

body.jadoo .btn:hover, body.jadoo .btn:active {
  opacity: .8;
  color: white;
}

body.jadoo .btn-yellow {
  box-shadow: 0px 20px 35px 0px #F1A50126;
  text-decoration: none;
  background-color: #F1A501;
}

body.jadoo .btn-orange-gradient {
  background: linear-gradient(180deg, #FF946D 0%, #FF7D68 100%);
  padding: 21px 47px;
}

body.jadoo .btn-play {
  text-decoration: none;
  display: inline-block;
  color: #686D77;
  font-size: 1.06rem;
  position: relative;
  padding: 16px 25px;
  padding-left: 70px;
}

body.jadoo .btn-play:before {
  content: '';
  background-image: url("../images/triangle.svg");
  background-repeat: no-repeat;
  display: inline-block;
  background-size: 15px;
  position: absolute;
  background-color: #DF6951;
  background-position: center;
  box-shadow: 0px 15px 30px 0px #DF69514D;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  left: 0;
  top: 3px;
}

body.jadoo .btn-play:hover:before, body.jadoo .btn-play:active:before {
  opacity: .8;
}

body.jadoo .form-control {
  color: #5E6282;
  border: none;
}

body.jadoo .form-input-area {
  display: flex;
  background-color: #fff;
